@import url(https://fonts.googleapis.com/css2?family=Nunito+Sans:wght@300;600;700&display=swap);
*,*::before,*::after{
  padding: 0;
  margin: 0;
  box-sizing: inherit;
  /* transition: all 100ms ease-out; */
}
html{
  font-size: 63.5%;
}
body{
  box-sizing: border-box;
  font-size: 1.4rem;
  font-family: 'Nunito Sans', sans-serif;
  background: hsl(0, 0%, 98%);
}

@media(max-width:768px){
 html{
   font-size: 43%;
 }

}

::-webkit-scrollbar{
  background: none;
  width: 1rem;
}
::-webkit-scrollbar-thumb{
  background: rgb(163, 163, 163);
  border-radius: 1rem;
}
.themeswitcher{
    display: flex;
    align-items: center;
    cursor: pointer;
}
.themeswitcher span{
    margin-right: 0.7rem;
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.11);
    min-height: 8rem;
    padding: 2rem 7rem;
}
.App{
  height: 100vh;
}
.search__container{
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  flex-wrap: wrap;
  padding: 3rem 0rem;
}
.main__content{
  padding: 3rem 7rem;
}
.card__container{
  display: grid;
  grid-template-columns: repeat(auto-fill, 30rem);
  grid-gap: 4.5rem;
}
.details__container{
  display: flex;
  font-size: 1.6rem;
  flex-wrap: wrap;
  margin-top: 2.5rem;
}
.image__box{
  width: 50%;
}
.image__box img{
  width: 70%;
}
.details__heading{
  margin: 2.5rem 0rem;
}
.details__paragraph{
  display: flex;
}
.writeup{
  width: 50%;
}
.details__paragraph div:last-child{
  margin-left: 7rem;
}
.details__paragraph p{
  padding: 0.6rem 0rem;
}
.details__pills{
  margin: 1.5rem 0rem;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.details__pills div{
  display: flex;
  align-items: center;
  margin: 1rem;
  flex-wrap: wrap;
}
.btn{
  display: flex;
  align-items: center;
  padding: 1rem 4rem;
  box-shadow: 1px 1px 10px rgba(0, 0, 0, 0.082);
  font-size: 1.6rem;
  cursor: pointer;
}
.btn span{
  margin-left: 1rem;
}
@media(max-width: 768px){
  .details__container{
    font-size: 2.5rem;
  }
  .image__box{
    width: 100%;
  }
  .image__box img{
    width: 100%;
  }
  .details__paragraph{
   flex-direction: column;
  }
  .writeup{
    width: 100%;
    margin-top: 3rem;
  }
  .details__paragraph div:last-child,.details__pills{
    margin-left: 0rem;
    margin-top: 3rem;
  }
  .details__pills div{
    margin: 0rem;
    margin-top: 1rem;
  }
}
.pills{
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding: 1rem 5rem;
    box-shadow: 1px 1px 15px rgba(0, 0, 0, 0.048);
}
.card{
    width: 30rem;
    box-shadow: 1px 1px 20px rgba(0, 0, 0, 0.075);
    border-radius: 0.7rem;
    min-height: 37rem;
    cursor: pointer;
    text-decoration: none;
}
.card:hover{
    transform: scale(1.03);
}
.card__header img{
    width: 100%;
    height: 20rem;
    border-radius: 0.7rem 0.7rem 0rem 0rem;
}
.card__body{
    padding: 1.5rem;
}
.heading{
    margin: 2rem 0rem;
}
.summary p{
    padding: 0.5rem 0rem;
}
.searchbox{
    display: flex;
    align-items: center;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content;
    padding: 0rem 1rem;
    box-shadow: 1px 1px 2rem rgba(78, 78, 78, 0.103);
    border-radius: 0.7rem;
    margin-bottom: 4rem;
}
.searchbox span{
    margin-left: 1rem;
    cursor: pointer;
}
.searchbox input{
    outline: none;
    background: none;
    border: none;
    padding: 2rem;
    width: 30rem;
}
.filter{
    display: flex;
    height: 5rem;
}
.filter select{
    outline: none;
    border: none;
    width: 16rem;
    padding: 1rem 2rem;
    box-shadow: 1px 1px 2rem rgba(78, 78, 78, 0.103);
    border-radius: 0.7rem;
    cursor: pointer;
}

